@import url(https://fonts.googleapis.com/css?family=Lobster);
html, body {
  margin: 0;
  padding: 0;
}

#SlotMachine{width:100vw;height:100vh;min-width:900px;min-height:700px;background-color:#09384c;position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
#SlotMachine .badge{position:relative;margin-top:30px;margin-bottom:200px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;width:650px;height:260px;border:10px solid #09384e;border-radius:50%;background-color:#ea5e56;-webkit-box-shadow:0px -5px 0px #f4f2e0;box-shadow:0px -5px 0px #f4f2e0;text-align:center
}
#SlotMachine .badge span{position:absolute;top:25px;color:#f4f2e0;font-size:70px;font-family:'Lobster', cursive
}
#SlotMachine .body{position:absolute;padding:50px;border:5px solid #09384e;border-radius:50px;background-color:#ea5e56;-webkit-box-shadow:5px 5px 0px #f4f2e0;box-shadow:5px 5px 0px #f4f2e0
}
#SlotMachine .window{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;border:5px solid #09384e;border-radius:40px;overflow:hidden;-webkit-transform:translateZ(0);transform:translateZ(0)
}
#SlotMachine .window::after,#SlotMachine .window::before{position:absolute;z-index:5;width:100%;height:100%;-webkit-box-shadow:0 10px 15px rgba(0,0,0,0.2) inset;box-shadow:0 10px 15px rgba(0,0,0,0.2) inset;content:' '
}
#SlotMachine .window::before{-webkit-box-shadow:0 -10px 15px rgba(0,0,0,0.2) inset;box-shadow:0 -10px 15px rgba(0,0,0,0.2) inset
}
#SlotMachine .window .container{width:600px;height:200px;position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-shadow:0 15px 20px rgba(0,0,0,0.3) inset;box-shadow:0 15px 20px rgba(0,0,0,0.3) inset;overflow:hidden
}
#SlotMachine .window .container .fence1,#SlotMachine .window .container .fence2{position:absolute;z-index:10;left:190px;width:10px;height:200px;border:3px solid #09384e;background-color:#ea5e56
}
#SlotMachine .window .container .fence2{left:400px
}
#SlotMachine .window .gift-style{font-family:'Lobster', cursive;color:#09384e;border-right:none;border-left:none;border-top:1px solid #333;border-bottom:1px solid #333;background-color:#f5f2e2
}
#SlotMachine .handler{position:absolute;top:50%;right:-55px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center
}
#SlotMachine .handler.active .ball{top:0px
}
#SlotMachine .handler.active .stick{top:-10px;height:20px
}
#SlotMachine .handler.disabled .ball{cursor:not-allowed;-webkit-animation:none;animation:none
}
#SlotMachine .handler .stick{position:absolute;top:-90px;right:-40px;width:20px;height:100px;border:5px solid #09384e;border-radius:20px;background-color:#ea5e56;-webkit-transition:350ms;transition:350ms
}
#SlotMachine .handler .stick2{position:absolute;top:-10px;right:-45px;width:80px;height:20px;border:5px solid #09384e;border-radius:0 20px 20px 0;background-color:#ea5e56
}
#SlotMachine .handler .ball{position:absolute;top:-155px;right:-65px;width:70px;height:70px;border:5px solid #09384e;border-radius:50%;background-color:#ea5e56;cursor:pointer;-webkit-transition:500ms;transition:500ms;animation:ballLight 1s infinite alternate-reverse linear
}
#SlotMachine .setting{margin-top:10px
}
#SlotMachine .setting span{color:#fff
}
#SlotMachine .select-button-group{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-wrap:nowrap;flex-wrap:nowrap;padding:15px
}
#SlotMachine .select-button{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-sizing:border-box;box-sizing:border-box;white-space:nowrap;margin:5px;padding:15px 30px;outline:none;border:none;border:solid 5px #09384e;border-radius:10px;background-color:#ea5e56;color:#f4f2e0;font-size:30px;font-family:'Lobster', cursive;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
#SlotMachine .select-button:after{position:absolute;padding:3px;width:100%;height:100%;border:solid 3px #f4f2e0;border-radius:10px;left:-6px;top:-6px;content:' '
}
#SlotMachine .resultList{position:fixed;top:0;right:0;bottom:0;left:0;z-index:9999;display:-webkit-box;display:-ms-flexbox;display:flex;visibility:hidden;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;background-color:rgba(0,0,0,0.4);opacity:0;-webkit-transition:500ms;transition:500ms
}
#SlotMachine .resultList.openSetting{visibility:visible;opacity:1
}
#SlotMachine .resultList-container{position:absolute;padding:20px;min-width:600px;height:400px;border:solid 5px #09384e;border-radius:30px;background-color:#f4f2e0;-webkit-box-shadow:5px 5px 15px rgba(0,0,0,0.4);box-shadow:5px 5px 15px rgba(0,0,0,0.4);overflow-y:auto
}
#SlotMachine .resultList-container .result{font-family:'Lobster', cursive;padding:15px;list-style:none;color:#09384e;font-size:16px
}
@-webkit-keyframes ballLight{
from{border:solid 5px #f4f2e0;-webkit-box-shadow:0px 0px 10px #f4f2e0;box-shadow:0px 0px 10px #f4f2e0
}
to{border:solid 5px #f4f2e0;-webkit-box-shadow:0px 0px 30px #f4f2e0;box-shadow:0px 0px 30px #f4f2e0
}
}
@keyframes ballLight{
from{border:solid 5px #f4f2e0;-webkit-box-shadow:0px 0px 10px #f4f2e0;box-shadow:0px 0px 10px #f4f2e0
}
to{border:solid 5px #f4f2e0;-webkit-box-shadow:0px 0px 30px #f4f2e0;box-shadow:0px 0px 30px #f4f2e0
}
}

.gift-container{-webkit-perspective:999999px;perspective:999999px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;margin-right:var(--width);-webkit-transform-style:preserve-3d;transform-style:preserve-3d
}
.gift-container *{-webkit-box-sizing:border-box;box-sizing:border-box
}
.gift-container .gift{position:absolute;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;width:var(--width);height:var(--height);border:1px solid #333;background-color:#fff;font-size:var(--fontSize)
}
.gift-container .gift img{padding:1px
}
.gift-container.flat{-webkit-transform:rotateX(var(--currentDeg));transform:rotateX(var(--currentDeg))
}
.gift-container.flat.autoTurn{-webkit-transition:var(--duration) ease-in-out;transition:var(--duration) ease-in-out;-webkit-transform:rotateX(var(--targetDeg));transform:rotateX(var(--targetDeg))
}
.gift-container.three-dimension{-webkit-transform:rotateY(var(--rotateY)) rotateX(var(--currentDeg));transform:rotateY(var(--rotateY)) rotateX(var(--currentDeg))
}
.gift-container.three-dimension.autoTurn{-webkit-transition:var(--duration) cubic-bezier(0.1, 0, 0, var(--rollBackDeg));transition:var(--duration) cubic-bezier(0.1, 0, 0, var(--rollBackDeg));-webkit-transform:rotateY(var(--rotateY)) rotateX(var(--targetDeg));transform:rotateY(var(--rotateY)) rotateX(var(--targetDeg))
}
